.liked-songs-link {
  position      : relative;
  display       : block;
  grid-column   : 1 / span 2;
  // max-width  : 100%;
  background    : linear-gradient(149.46deg, #450af5, #8e8ee5 99.16%);

  @include for-inbetween-layout {
    grid-column: unset;
  }

  &:hover {
    // background-color: #282828;

    .liked-songs-play-icon-container {
      visibility: visible;
      opacity   : 1;
      cursor    : pointer;
    }
  }
}


.liked-songs-play-icon-container {
  visibility      : hidden;
  opacity         : 0;
  background-color: #1db954;
  color           : white;
  position        : absolute;
  width           : 3.5rem;
  height          : 3.5rem;
  border-radius   : 50%;
  bottom          : 20px;
  right           : 20px;
  transition      : visibility 0s, opacity 0.3s linear;

  &:hover {
    transform: scale(1.1);
  }

  // cursor    : default;
}